.name {
    font-family: "tasty_morselregular", cursive;
    font-size: 36px;
    text-align: left;
    color: #fff;
    opacity: 0.8;
    display: flex;
    align-items: center;
    justify-content: left;
    position: relative;
    line-height: 120%;

    & > div {
        // margin-left: 54px;

        &::before {
            content: "";
            position: absolute;
            width: 54px;
            height: 50px;
            left: -10px;
            top: 50%;
            transform: translate(0%, -50%);
            // background-image: url("../img/heart.svg");
            background-size: auto 100%;
            background-repeat: no-repeat;
        }
    }
}
