.wrapper {
    width: 210mm;
    height: 297mm;
    margin: 0 auto;
    background-image: url(../img/noise.png), linear-gradient(297.63deg, #326295 18.25%, #1e3d5e 61.4%);
    border-radius: 2mm;

    @media print {
        @page {
            margin: 0;
            size: auto;
        }
        & {
            background: none;
        }
    }
}
